.reviews .slick-slide .role { color: #c1c1c1; font-family: "MONO-RGOMODULAR-Regular", Arial, sans-serif; font-size: 14px; font-weight: 600; line-height: 46px; }
.reviews .slick-list { margin: 0 95px; box-sizing: border-box; *zoom: 1; }
.reviews .slick-list:after { content: " "; display: table; clear: both; }

footer.page-footer { background-color: #141414; }
footer.page-footer #map { background: url("../img/map.png") 0 0 no-repeat; height: 590px; position: relative; right: -44.3%; }
footer.page-footer #map .pin i { color: #fc291c; font-size: 40px; cursor: pointer; }
footer.page-footer #map #pin-1 { position: absolute; top: 147px; left: 36px; }
footer.page-footer #map #pin-2 { position: absolute; top: 224px; left: 171px; }
footer.page-footer p { color: #525252; font-family: "PFBeauSansPro-Regular", Arial, sans-serif; font-size: 18px; font-weight: 400; line-height: 24px; margin: 30px 0 50px 0; }
footer.page-footer #street-2, footer.page-footer #street-1 { cursor: pointer; }
footer.page-footer .sub-heading { font-family: "mSemiBold", Arial, sans-serif; color: #525252; font-size: 14px; font-weight: 600; line-height: 46px; display: block; }
footer.page-footer .city { color: white; font-family: "PFBeauSansPro-SemiBold", Arial, sans-serif; font-size: 18px; font-weight: 600; line-height: 30px; display: block; cursor: pointer; }
footer.page-footer .street { font-family: "Roboto-Regular", Arial, sans-serif; font-weight: 400; color: #a3a3a3; font-size: 14px; font-weight: 400; display: inline-block; cursor: pointer; }
footer.page-footer .label { color: #525252; font-family: "mSemiBold", Arial, sans-serif; font-size: 14px; font-weight: 600; line-height: 46px; display: inline-block; }
footer.page-footer .phone { font-family: "MONO-RGOMODULAR-Regular", Arial, sans-serif; color: #fdfdfd; font-size: 22px; font-weight: 400; line-height: 2.5rem; }
footer.page-footer .email { font-family: "PFBeauSansPro-Bbook", Arial, sans-serif; font-size: 14px; font-weight: 300; position: relative; color: #02a2db; line-height: 1; display: inline-block; margin-top: 15px; }
footer.page-footer .social { display: inline-block; position: relative; width: 42px; height: 42px; margin-right: 15px; border: 2px solid white; border-radius: 50%; }
footer.page-footer .social:hover { border: 2px solid #3d6092; }
footer.page-footer .social .icon-vk { position: absolute; top: 50%; margin-top: -6px; left: 50%; margin-left: -11px; }
footer.page-footer .social .icon-instagram { position: absolute; top: 50%; margin-top: -11px; left: 50%; margin-left: -11px; }
footer.page-footer .social .icon-facebook { position: absolute; left: 50%; margin-left: -5px; top: 50%; margin-top: -9px; }
footer.page-footer .social.insta:hover { border: 2px solid #bc168b; }
footer.page-footer .social.facebook:hover { border: 2px solid #303aa5; }
footer.page-footer .footer-copyright { background: #262626; padding: 29px 0; }
footer.page-footer .footer-copyright span { font-size: 14px; font-family: "mSemiBold", Arial, sans-serif; color: #c1c1c1; font-weight: 600; line-height: 2.2rem; }
footer.page-footer .instagram { width: 100%; box-sizing: border-box; *zoom: 1; }
footer.page-footer .instagram:after { content: " "; display: table; clear: both; }
footer.page-footer .instagram .row { margin-bottom: 0; }
footer.page-footer .instagram .item { text-align: center; }
@media only screen and (min-width: 991px) { footer.page-footer .instagram .item { float: left; width: 12.45%; overflow: hidden; *zoom: 1; box-sizing: border-box; }
  footer.page-footer .instagram .item:after { content: " "; display: table; clear: both; } }

.t40 { margin-top: 90px; margin-bottom: 80px; }

.header-mobile { display: none; }

.mobile .header-fixed, .mobile #map, .mobile .header__top { display: none; }

.mobile .header-mobile { display: block; position: fixed; left: 0; right: 0; top: 0; z-index: 1000; }
.mobile .header-mobile__main { height: 64px; background-color: #0c0c0c; }


